import React from 'react';
import { Reset } from 'styled-reset'
import styled from 'styled-components';
import {Link} from 'react-router-dom'
import {useState} from 'react'
export const DropdownView = styled.div.attrs((props) => ({
  // attrs 메소드를 이용해서 아래와 같이 div 엘리먼트에 속성을 추가할 수 있다.
  role: 'dialog',
}))`
  width: 150px;
  border: 1px solid #4C4B48;
  position:absolute;
  background: #FFF;
  box-shadow: 0px 6px 10px 0px rgba(0, 0, 0, 0.15);
  z-index:1;
  top:29px;
  left:0;
    >div.dropcont {
     width: 150px;
      color: #000;
      font-size: 16px;
      font-weight: 400;
      line-height: 40px; /* 250% */
      li{
        width: auto;
        padding:0 20px;
        &:nth-child(even){
          background:#F0ECE4;
        }
      }
    }
`;
function DropDown(props) {
  const [isOpen, setIsOpen] = useState(false);

  const dropdownHandler = (props) => {
    setIsOpen(!isOpen) 
  };
  const users = [
    {
        id:1,
        date : "2024년 10월1일",
        date2 : "2024년 10월2일"
    },
    {
        id:2,
        username : "jisung",
        email : "jisung@gmail.com"
    }
  ]
  return (
      <div className='statevalue' onClick={dropdownHandler}>
        All
        {isOpen ? 
          <DropdownView onClick={(e) => e.stopPropagation()}>
            <div className='dropcont'>
              <ul>
                <li>{users[0].id}</li>
                <li>{users[0].date}</li>
                <li>{users[0].date2}</li>
              </ul>
            </div>
          </DropdownView>
        : null
       }
      </div>
  );
}

export default DropDown;